@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: v.$verde;

    @include m.tablet{
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .1rem #333;
        }
    }

    h1{
        color: v.$blanco;
        text-align: center;
        margin: 0 0 2rem 0;

        @include m.tablet{
            margin: 0;
        }
    }

    .contenido-header{
        @include m.tablet{
            display: flex;
            justify-content: space-between;
            padding: 2rem;
            align-items: center;
        }
    }
}

.navegacion-principal{
    a{
        color: v.$blanco;
        font-size: 2.4rem;
        display: block;
        text-align: center;
        margin: 0 0 1rem 0;
        &:hover{
            color: v.$amarillo;
        }
    }

    @include m.tablet{
        display: flex;
        gap: 2rem;
        margin: 0;
    }
}

