@use 'variables' as v;
@use 'base/mixins' as m;

html{
    box-sizing: border-box;
    font-size: 62.5%;
}

*, *::before, *::after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;

    &.fijar-body{
        overflow: hidden;
    }

    &.body-scroll{
        padding-top: 8.6rem;
    }
}

p{
    font-size: 2rem;
    color: v.$negro;
}

a{
    text-decoration: none;
}

.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

h1,h2,h3{
    margin: 0 0 5rem 0;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.5rem;
}

h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    display: block;
    width: 100%;
    height: auto;
}